body{
    margin: 50px auto;
    width: 50%;
    text-align: center;
    background-color: #337534;
    opacity: 0.8;
    background: radial-gradient(circle, transparent 20%, #337534 20%, #337534 80%, transparent 80%, transparent), radial-gradient(circle, transparent 20%, #337534 20%, #337534 80%, transparent 80%, transparent) 25px 25px, linear-gradient(#3c794a 2px, transparent 2px) 0 -1px, linear-gradient(90deg, #3c794a 2px, #337534 2px) -1px 0;
    background-size: 50px 50px, 50px 50px, 25px 25px, 25px 25px;

}

.hand {
    display: flex;
    justify-content: center;
}

.card {
    border: 1px solid;
    margin: 15px;
    width: 70px;
    height: 120px;
    border-radius: 5px;
    background-color: #fff;

    font-weight: 800;
    padding-top: 5px;
    box-shadow: 5px 5px 10px  #050505;
}

.cardBack{
    border: 1px solid;
    margin: 15px;
    width: 70px;
    height: 120px;
    border-radius: 5px;

    background-color: #d9ebd9;
    opacity: 0.8;
    background-image: linear-gradient(to right, #c51d39, #c51d39 5px, #d9ebd9 5px, #d9ebd9 );
    background-size: 10px 100%;
    box-shadow: 5px 5px 10px  #050505;

}

button {
    border-radius: 5px;
    /* background-color: #7eb0df; */
    background-image: linear-gradient(#357abb , #032541);
    color: #daecfa;
    border: none;
    min-height: 40px;
    margin: 15px;
    width: 100px;
    cursor: pointer;
    box-shadow: 5px 5px 10px  #050505;
}

.startButtons{
    display: flex;
    justify-content: center;
}

h2, .playerHandInfo, .dealerHandInfo, .playerBank {
    background-image: linear-gradient(#1a7064 , #01070a);
    border-radius: 7px;
    color: #f5e886;
    font-size: 24px;
    font-weight: bold;
    box-shadow: 2px 2px 10px #050505;

  }

  .playerBank {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
  }

  .playerHandInfo, .dealerHandInfo {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }


